import axios from "./index"
function parse() {
  this.name = "ss"
}
parse.prototype.parseurl = function (moduleName, urlObj) {
  var obj = this[moduleName] = {}
  Object.keys(urlObj).forEach(v => {
    obj[v] = this.send.bind(this, moduleName, v, urlObj[v])
  })
}
parse.prototype.send = function (moduleName, apiName, url, config) {
  var type = config.type
  axios[type](url).then(res => {
    console.log(res);
  }).catch(v => {
    console.log(v);
  })
}
export default new parse